<template>
    <div>
        <form action="/">
            <van-search
                    v-model="keyword"
                    placeholder=""
                    show-action
                    @search="onSearch"
                    @cancel="onCancel"
                    @focus="showList = false"
            />
        </form>
        <!---->
        <div v-if="!showList">
            <div v-if="hotList.length > 0">
                <van-cell-group title="">
                    <van-cell clickable :to="'/detail?id='+item.id+'&from=search'" v-for="(item, index) in hotList" :key="index">
                        <template slot="title">
                            <van-tag type="danger">{{index+1}}</van-tag>
                            <span style="margin-left: .5rem">{{item.title}}</span>
                        </template>
                    </van-cell>
                </van-cell-group>
            </div>
        </div>
        <!---->
        <div v-else>
            <my-loading v-if="isLoading"></my-loading>
            <div v-else class="search-container">
                <!---->
                <div v-if="emptyData" class="empty">
                    <span></span>
                </div>
                <!---->
                <div id="list" v-if="videos.length > 0">
                    <div class="res">
                        <span>{{totalCount}}</span>
                    </div>
                    <van-list
                            v-model="isLoadingMore"
                            :finished="noMoreData"
                            finished-text=""
                            @load="changePage"
                            :offset="50"
                    >
                        <movie-card2 v-for="(video, index) in videos" :video="video" :key="index" from="search">
                        </movie-card2>
                    </van-list>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import MyLoading from '../components/Loading';
    import MovieCard2 from "../components/MovieCard2";

    export default {
        name: "Search",
        components: {
            MyLoading,
            MovieCard2,
        },
        mounted() {
            this.setHeader();
            this.getHotSearch();
        },
        updated() {
            this.setHeader();
        },
        activated() {
            this.setHeader();
        },
        data() {
            return {
                keyword: '',
                isLoading: false,
                isLoadingMore: false,
                isLoadingHot: false,
                currentPage: 1,
                totalPage: 0,
                totalCount: 0,
                videos: [],
                emptyData: false,
                noMoreData: false,
                showList: false,
                hotList: [],
            }
        },
        methods: {
            getHotSearch() {
                this.showList = false;
                this.isLoadingHot = true;
                this.$get(this.API.hot, {})
                    .then((res) => {
                        this.isLoadingHot = false;
                        if(res.retCode === 0){
                            this.hotList = res.data.list;
                        }
                    })
            },
            onSearch() {
                this.isLoading = true;
                this.emptyData = false;
                this.noMoreData = false;
                this.showList = true;
                this.videos = [];
                this.$get(this.API.search, {keyword: this.keyword}, this.currentPage)
                    .then((res) => {
                        this.isLoading = false;
                        if(res.retCode === 0){
                            if(res.data.list.length === 0){
                                this.emptyData = true;
                                return;
                            }
                            this.videos = res.data.list;
                            this.totalPage = res.data.totalPage;
                            this.totalCount = res.data.totalCount;
                        }
                    })
            },
            //
            changePage() {
                if(this.currentPage >= this.totalPage){
                    this.isLoadingMore = false;
                    this.noMoreData = true;
                    return false;
                }
                this.currentPage += 1;
                this.isLoadingMore = true;
                this.$get(this.API.search, {keyword: this.keyword}, this.currentPage)
                    .then((res) => {
                        this.isLoadingMore = false;
                        if(res.retCode === 0){
                            if(res.data.list.length === 0){
                                this.noMoreData = true;
                                return;
                            }
                            res.data.list.map((item, index) => {
                                this.videos.push(item);
                            })
                            this.totalPage = res.data.totalPage;
                            this.totalCount = res.data.totalPage;
                        }
                    })
            },
            onCancel() {
                this.showList = false;
                this.$router.go(-1)
            },
            setHeader() {
                let data = {
                    title: '',
                    showBack: true,
                    leftText: '',
                    clickLeft: () => {this.$router.go(-1)},
                    rightText: '',
                    clickRight: '',
                }
                this.$emit('changeHeader', data)
            },
        }
    }
</script>

<style scoped>
    .search-container {
        background-color: #f5f5f5;
    }
    .more {
        margin: 1rem 0 3rem 0;
        text-align: center;
    }
    .empty {
        margin-top: 3rem;
        text-align: center;
    }
    .res {
        margin-top: .3rem;
        text-align: center;
    }
    .empty span, .res span{
        color: #666;
        font-size: .8rem;
    }

</style>


