<template>
    <div class="error">
        <p> <span v-if="errorMsg.length > 0">: {{errorMsg}}</span></p>
        <van-button type="primary" @click="tryAgain"></van-button>
    </div>
</template>

<script>
    export default {
        props: ['errorMsg', 'tryAgain', 'handleTry'],
        name: "Error",
    }
</script>

<style scoped>

</style>


